<template>
	<view class="pages">
		<view class="hadersboxs">
			<view class="haderscontents">
				<view class="" @click="back">
					<image class="imgsss"
						src="data:image/png;base64,"
						mode=""></image>
				</view>
				<!-- <view class="">{{data.pointname}} -趋势图</view> -->
				<view class=""> -趋势图</view>
				<view class=""> </view>
			</view>
		</view>


		<view class="leave_cont">
			<view class="ul">
				<view class="li" @click="openDatePicker('stime')">
					<text>开始时间:</text>
					<view class="flex1">

						<view class="date">{{stime}}</view>
					</view>
				</view>
				<view class="li" @click="openDatePicker('etime')">
					<text>结束时间:</text>
					<view class="flex1">

						<view class="date">{{etime}}</view>
					</view>
				</view>
			</view>

			<view class="headerrights">
				<view class="rights-lefts" @click="query">
					<image class="leftsimgs"
						src="data:image/png;base64,"
						mode=""></image>
					<text>查询</text>
				</view>

			</view>
		</view>
		<view class="content">
			<view class="charts-box">
				<qiun-data-charts class="chartsed" type="line" :opts="opts" :chartData="chartData" :ontouch="true"
					:onzoom="true" :disableScroll="true" />
			</view>
		</view>
		<!-- 单选框 -->
		<view class="groupbox">
			<u-radio-group v-model="value" @change="radioGroupChange" size="20px">
				<u-radio label-size="15px" icon-size="5px" @change="radioChange" v-for="(item, index) in list"
					:key="index" :name="item.name" :title="item.title" :disabled="item.disabled">
					{{item.title}}
				</u-radio>
			</u-radio-group>
		</view>

		<u-picker mode="time" :default-time="defaultTime" v-model="show" :params="params" @confirm="confirm"></u-picker>
	</view>
</template>

<script>
	import {
		oilPointtrend
	} from "@/api/api.js"
	export default {
		data() {

			return {
				unitsed: '',
				list: [{
						title: '通道1',
						disabled: false,
						name: "1"
					},
					{
						title: '通道2',
						disabled: false,
						name: "2"
					},
					{
						title: '通道3',
						disabled: false,
						name: "3"
					},
					{
						title: '通道4',
						disabled: false,
						name: "4"
					},
					{
						title: '通道5',
						disabled: false,
						name: "5"
					},
					{
						title: '通道6',
						disabled: false,
						name: "6",
					},
					{
						title: '通道7',
						disabled: false,
						name: "7",
					},
					{
						title: '通道8',
						disabled: false,
						name: "8",
					},
					{
						title: '温度',
						disabled: false,
						name: "9",
					},
					{
						title: '湿度',
						disabled: false,
						name: "10",
					}
				],
				// u-radio-group的v-model绑定的值如果设置为某个radio的name，就会被默认选中
				defaultTime: '', //默认时间
				value: '2',
				params: {
					year: true,
					month: true,
					day: true,
					hour: true,
					minute: true,
					second: false
				},
				show: false, //时间选择器显示隐藏
				stime: "",
				etime: "",
				currentTime: '',
				chartData: {},

				opts: {
					rotate: false,
					rotateLock: false,
					enableScroll: true,
					enableMarkLine: true,
					color: ["#1890FF"],
					dataPointShape: false,
					dataLabel: false,
					padding: [10, 55, 20, 68],
					legend: {
						show: false //去点name名称
					},
					xAxis: {
						splitNumber: 2,
						gridEval: 1,
						rotateLabel: true,
						rotateAngle: 0.0001,
						fontSize: 10,
						type: 'grid',
						gridType: 'dash',
						"labelCount": 5,
						disableGrid: true,
						boundaryGap: "justify",
						scrollShow: true,
						itemCount: 500000,
						scrollAlign: 'bottom', //滚动条初始位置
						scrollBackgroundColor: '#262b41', //默认为 #EFEBEF
						scrollColor: '#262b41', //默认为 #A6A6A6
						dashLength: 4,
						scrollAlign: 'right',
					},
					yAxis: {
						fontSize: 12,
						gridType: "dash",
						dashLength: 1,
						tofix: 3
					},
					extra: {
						line: {
							type: "straight",
							width: 1
						}
					}
				},

			};
		},
		onLoad(options) {
			// #ifdef APP-PLUS
			setTimeout(() => {
				plus.screen.lockOrientation("landscape-primary");
			}, 300);
			// #endif
			this.stime = this.change_date(3);
			this.etime = this.change_date(0);
			this.oilPoinlist(this.pointcode)


		},
		onShow() {


		},
		onUnload() {
			// #ifdef APP-PLUS
			plus.screen.lockOrientation('portrait-primary');
			// #endif

		},
		onReady() {
			this.getServerData();
		},
		methods: {
			//获取数据
			oilPoinlist(pointcode) {
				oilPointtrend().then(res => {
					console.log(res, 12)
				})
			},




			//时间
			change_date(days) {
				// 参数表示在当前日期下要增加的天数
				var now = new Date();
				// + 1 代表日期加，- 1代表日期减
				now.setDate((now.getDate() + 1) - 1 * days);
				var year = now.getFullYear();
				var month = now.getMonth() + 1;
				var day = now.getDate();

				var hour = now.getHours();
				var minute = now.getMinutes();
				var second = now.getSeconds();
				if (hour < 10)
					hour = '0' + hour;
				if (minute < 10)
					minute = '0' + minute;
				if (second < 10)
					second = '0' + second;
				if (month < 10) {
					month = '0' + month;
				}
				if (day < 10) {
					day = '0' + day;
				}
				return year + '-' + month + '-' + day + " " + hour + ":" + minute + ":" + second;
			},
			openDatePicker(time) {
				this.show = !this.show
				this.currentTime = time
			},
			confirm(time) {
				console.log(time)
				let {
					year: y,
					month: m,
					day: d,
					hour: h,
					minute: mm
				} = time
				console.log(`${y}/${m}/${d} ${h}:${mm}`)
				this[this.currentTime] = `${y}/${m}/${d} ${h}:${mm}`
			},

			// 选中某个单选框时，由radio时触发
			radioChange(value) {
				this.value = value
				// this.gatDatalist(this.pointcode)
				console.log(this.value, "单选选中")

			},


			// 选中任一radio时，由radio-group触发
			radioGroupChange(value) {
				// console.log(this.vel,"11")
			},




			//点击查询
			query() {
				// this.gatDatalist(this.pointcode)
				// console.log(this.gatDatalist, '010')
			},






			getServerData() {
				setTimeout(() => {


					let res = {
						categories: ["1", "2", "3", "4", "5", "6"],
						series: [{
								name: "",
								data: [1, 30, 1, 50, 1, 30, 1, 50]
							},

						]
					};
					this.chartData = JSON.parse(JSON.stringify(res));
				}, 500);
			},



			back() {
				uni.navigateBack({
					delta: 1
				})
			},




		}
	};
</script>

<style lang="scss" scoped>
	.charts-box {
		color: #AFDDFF;
		width: 100%;
		height: 215px;
	}

	.chartsed {
		width: 100%;

	}

	.groupbox {
		display: flex;
		align-items: center;
		justify-content: space-around;
	}

	.uradios {
		color: #007AFF;
	}

	.pages-hareder {
		width: 99%;
		height: 50px;
		background-color: #007AFF;
	}


	@import "index.scss";
</style>
